section#leave {
    .container-1000 {
        padding: 50px 0 0;

        @media (min-width: 768px) {
            padding: 100px 0 0;
        }
    }

    .module_pagetitle {
        border-bottom: 1px solid #aaa;
    }

    .information-wrapper {
        padding: 30px 16px 80px;
        line-height: 1.6;
    }

    .description {
        font-size: 14px;
    }

    .input-button {
        width: 90px;
        margin: 0 auto;
        height: 32px;
        background-color: white;
        color: #333;
        border: 1px solid #aaa;
    }
}
