section#pw-change,
section#mypage,
section#find-account,
section#find-member,
section#sign-in,
section#sign-up {
    // background-color: #ebf6ff;
    // min-height: 100vh;

    .input-button {
        background-color: lightsteelblue;
    }

    article {
        width: auto;
        margin: 0 auto;
        padding: 15px 16px 50px;

        @media (min-width: 768px) {
            width: 360px;
            padding: 40px 0 70px;
        }
    }

    .account-form {
        padding: 24px 0;
    }

    .heading {
        color: #0077d9;
        line-height: 1em;
        font-weight: 500;
        font-size: 17px;
        letter-spacing: -0.03em;
        text-align: center;
        padding-bottom: 12px;
    }

    .sponsor-group {
        .title {
            font-size: 12px;
            color: #666;
            margin-bottom: 6px;
            padding-top: 3px;
            padding-left: 3px;
        }
    }

    .radio-group {
        display: flex;
    }

    .radio-wrapper {
        flex: 1;
        margin-right: -1px;

        span {
            position: relative;
            z-index: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #666;
            font-size: 15px;
            height: 48px;
            border: 1px solid #d6d6d6;
            background-color: white;

            &:hover {
                border: 1px solid #9ea7ad;
                z-index: 2;
            }
        }

        &:last-child > span {
            border-bottom-right-radius: 4px;
            border-top-right-radius: 4px;
        }
        &:first-child > span {
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px;
        }

        & > input[type='radio']:checked + span {
            background-color: #dbefff;
        }
    }

    .sign-up-btn {
        margin-top: 20px;
    }
}

section#sign-in {
    article {
        padding: 115px 16px 50px;

        @media (min-width: 768px) {
            padding: 130px 0 70px;
        }
        @media (min-width: 1680px) {
            padding: 180px 0 70px;
        }
    }
    .sign-up-btn {
        background-color: rgb(250, 250, 250);
        color: #666;
        box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
        margin-top: 10px;
    }

    .find-group {
        position: relative;
        margin-top: -5px;

        & > a {
            position: relative;
            font-size: 12px;
            color: #738096;
        }

        // & > a:first-child {
        //     margin-right: 0.5rem;
