        //         right: 0;
        //         transform: translateY(-45%);
        //         border-right: 1px solid #738096;
        //         height: 80%;
        //     }
        // }
    }
}

section#find-account {
    .heading {
        color: #666;
        line-height: 1em;
        // font-weight: 500;
        font-size: 16px;
        letter-spacing: -0.03em;
        text-align: center;
        padding-bottom: 12px;
    }

    .divide-line {
        border-bottom: 1px solid lightgray;
        padding-bottom: 50px;
        margin-bottom: 50px;
    }
}

section#mypage {
    h2.module_pagetitle {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .leave-btn {
        @include flexCentered();
        width: 90px;
        height: 30px;
        margin-bottom: -5px;
        background-color: white;
        color: #666;
        font-size: 13px;
        border: 1px solid rgb(189, 188, 188);
        border-radius: 4px;
    }

    .change-box {
        @include flexCentered();
        padding: 0 40px 0 40px;
        margin-top: -7px;
        color: rgb(9, 9, 175);
        text-decoration: underline;
    }
}

section#pw-change {
    .cancel-btn {
        background-color: white;
        color: #333;
        border: 1px solid #aaa;
    }
}
