footer#footer {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 99;
    width: 100%;
    height: $footerHeight;
    padding: 40px 14px 0 14px;
    background-color: #ebebeb;
    transition: $headerDuration;
    font-size: 13px;
    line-height: 1.5;

    @media (min-width: 768px) {
        font-size: 14px;
    }

    &.flip {
        transform: translateX(-100%);
    }
    a {
        position: relative;
        display: inline-block;
        padding-right: 30px;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            display: inline-block;
            width: 1px;
            height: 100%;
            border-right: 1px solid #888;
            margin-right: 15px;
        }
    }
}
