section#term {
    .container-1000 {
        padding: 40px 10px;
    }

    hgroup {
        h1 {
            text-align: center;
            letter-spacing: -0.03em;
            word-spacing: 0.2em;
            font-size: 24px;
            font-weight: 500;

            @media (min-width: 768px) {
                font-size: 32px;
            }
        }
    }

    article {
        max-width: 700px;
        padding: 24px 20px;
        margin: 30px auto 0;
        border: 1px solid #ccc;
        font-size: 13px;
        line-height: 1.8;
        color: #333;

        @media (min-width: 768px) {
            padding: 40px 30px;
        }
    }
}
