header#header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: $headerHeight;
    background-color: rgba(255, 255, 255, 0);
    padding: 0 12px;
    transition: $headerDuration;

    &.flip {
        transform: translateX(-100%);
    }

    @media (min-width: 768px) {
        padding: 0 24px;
    }

    @media (min-width: 1024px) {
        height: $headerHeight + 10px;
    }

    .container-1000 {
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
    }

    .logo {
        img {
            width: 60px;

            @media (min-width: 768px) {
                width: 70px;
            }
            @media (min-width: 1024px) {
                width: 80px;
            }
        }
    }

    nav {
        display: none;

        @media (min-width: 1024px) {
            display: block;
        }
    }

    nav {
        padding-left: 120px;

        ul {
            display: flex;
            font-size: 15px;
            font-weight: 500;

            li {
                margin-right: 100px;

                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    .another-wrapper {
        display: none;

        @media (min-width: 1024px) {
            display: flex;
            align-items: center;
            position: absolute;
            right: 0;
        }

        & > * {
            display: flex;
            align-items: center;

            & > * {
                background-color: rgba(255, 255, 255, 0);
                margin-right: 28px;
                font-size: 12px;
                color: #666;

                b {
                    padding-left: 5px;
                    &::before {
                        content: '[';
                    }
                    &::after {
                        content: ']';
                    }
                }
            }

            &:last-child > * {
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    .hamburger {
        position: absolute;
        right: 0;

        @media (min-width: 1024px) {
            display: none;
        }
    }
}
