.pagination-wrapper {
    position: relative;
    height: 80px;

    ul {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;

        li {
            border: none !important;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 15px;
            transition: 0.2s;
            min-width: 28px;
            min-height: 28px;
            max-width: 28px;
            max-height: 28px;
            margin-right: 12px;
            color: #999;
            cursor: pointer;

            &:hover {
                color: #555;
                box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.1);
                border-radius: 50%;
            }

            &.active {
                font-weight: bold;
                color: #1f7ce6;
                transform: scale(1.1);
                box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
                border-radius: 50%;
            }

            &:first-child,
            &:nth-child(2),
            &:nth-child(8),
            &:last-child {
                opacity: 0.4;
                transition: 0.2s;

                &:hover {
                    opacity: 0.8;
                }
            }

            &:first-child {
                content: '';
                background-image: url('https://icongr.am/feather/chevrons-left.svg?size=20&color=000000');
                background-repeat: no-repeat;
                background-position: center;
                margin-right: 5px;
            }
            &:nth-child(2) {
                content: '';
                background-image: url('https://icongr.am/feather/chevron-left.svg?size=20&color=000000');
                background-repeat: no-repeat;
                background-position: center;
            }
            &:nth-child(8) {
                content: '';
                background-image: url('https://icongr.am/feather/chevron-right.svg?size=20&color=000000');
                background-repeat: no-repeat;
                background-position: center;
                margin-right: 5px;
            }
            &:last-child {
                content: '';
                background-image: url('https://icongr.am/feather/chevrons-right.svg?size=20&color=000000');
                background-repeat: no-repeat;
                background-position: center;
                margin-right: 0;
            }
        }
    }
}
