main#main {
    padding-top: $headerHeight;
    padding-bottom: $footerHeight;
    height: 100%;
    position: relative;
    overflow-x: hidden;
    transition: $headerDuration;

    @media (min-width: 1024px) {
        padding-top: $headerHeight + 10px;
    }

    &.flip {
        transform: translateX(-100%);
    }

    h2.module_pagetitle {
        width: 100%;
        padding: 15px 16px 14px 16px;
        box-sizing: border-box;
        background-color: white;

        @media (min-width: 1024px) {
            padding-top: 0;
        }

        span {
            position: relative;
            width: 100%;
            height: 1.3em;
            line-height: 1.4em;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            word-wrap: break-word;
            word-break: break-all;
            white-space: normal;
            font-weight: 500;
            font-size: 20px;
            color: #303538;
        }
    }
}

section#index {
    position: relative;
    background-color: white;

    article.bg-wrapper {
        margin-top: -$headerHeight;
        position: relative;
        height: 100vh;
        background-image: url('https://images.unsplash.com/photo-1534805406629-e92418bcab97?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2700&q=80');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        overflow: hidden;

        @media (min-width: 1024px) {
            margin-top: -$headerHeight - 10px;
            background-attachment: fixed;
        }

        .brand-box {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .logo-svg {
                width: 200px;

                @media (min-width: 768px) {
                    width: 300px;
                }

                svg {
                    width: 100%;
                    fill: white;
                }
            }
        }
    }

    #index-contents-container {
        position: relative;

        .bg-box {
            position: absolute;
            z-index: 1;

            img {
                width: 100%;
            }

            &.bg-001 {
                top: 1%;
                right: -30%;
                width: 280px;

                @media (min-width: 768px) {
                    width: 600px;
                }

                @media (min-width: 1023px) {
                    width: initial;
                }
            }
            &.bg-002 {
                top: 10%;
                left: -50%;
                width: 280px;

                @media (min-width: 768px) {
                    top: 6%;
                    left: -50%;
                    width: 600px;
                }

                @media (min-width: 1024px) {
                    top: 8%;
                    left: -20%;
                    width: initial;
                }
            }
            &.bg-003 {
                top: 31%;
                right: -2%;
                width: 200px;

                @media (min-width: 768px) {
                    top: 32%;
                    right: -2%;
                    width: 300px;
                }

                @media (min-width: 1024px) {
                    top: 32%;
                    right: -2%;
                    width: initial;
                }
            }
            &.bg-004 {
                top: 60%;
                left: -22%;
                width: 280px;

                @media (min-width: 768px) {
                    top: 61%;
                    left: -35%;
                    width: 600px;
                }

                @media (min-width: 1023px) {
                    top: 59%;
                    left: -20%;
                    width: initial;
                }
            }
            &.bg-005 {
                top: 75%;
                right: -1%;
                width: 230px;

                @media (min-width: 768px) {
                    top: 76%;
                    right: -1%;
                    width: 400px;
                }

                @media (min-width: 1023px) {
                    top: 75%;
                    right: -1%;
                    width: initial;
                }
            }
        }

        article.each-wrapper {
            position: relative;
            z-index: 2;
            padding: 74px 0;
            border-bottom: 1px solid #ddd;

            @media (min-width: 768px) {
                padding: 102px 0;
            }

            .container-1000 {
                padding: 0 14px;

                @media (min-width: 768px) {
                    padding: 0 24px;
                }

                @media (min-width: 1366px) {
                    padding: 0;
                }
            }

            hgroup {
                h2 {
                    font-size: 24px;
                    font-weight: bold;
                    line-height: 1.5;

                    @media (min-width: 768px) {
                        font-size: 40px;
                    }

                    p {
                        font-size: 13px;
                        font-weight: normal;
                        line-height: 1.5;
                        padding-top: 5px;

                        @media (min-width: 768px) {
                            font-size: 16px;
                            padding-top: 0;
                            padding-left: 8px;
                        }
                        @media (min-width: 1024px) {
                            display: inline-block;
                        }

                        .red {
                            color: #cc2626;
                        }
                    }
                }
            }

            .condition-box {
                padding: 24px 0 24px 16px;

                @media (min-width: 768px) {
                    padding: 34px 0 34px 34px;
                }

                ul {
                    li {
                        margin-bottom: 18px;

                        @media (min-width: 768px) {
                            margin-bottom: 24px;
                        }

                        &:last-child {
                            margin-bottom: 0;
                        }

                        figure {
                            display: flex;
                            align-items: flex-start;

                            img {
                                width: 18.64px;
                                object-fit: contain;
                                margin-top: -1px;

                                @media (min-width: 768px) {
                                    width: 28.45px;
                                    margin-top: -3.5px;
                                }
                            }

                            figcaption {
                                font-size: 14px;
                                padding-left: 7px;

                                @media (min-width: 768px) {
                                    font-size: 18px;
                                    padding-left: 10px;
                                }
                            }
                        }
                    }
                }
            }

            .example-box {
                .mobile {
                    margin: 0 -14px;
                    color: #61a0d8;

                    @media (min-width: 768px) {
                        display: none;
                    }

                    & > button {
                        color: #61a0d8;
                        padding-left: 14px;
                        padding-bottom: 14px;
                        background-color: rgba(0, 0, 0, 0);

                        &.visibility {
                            &::before {
                                content: '예시 보기 〉';
                            }

                            & ~ .swiper-container {
                                opacity: 0;
                                height: 0;
                                padding: 0;
                                // overflow: hidden;
                            }
                        }

                        &.hide {
                            position: relative;
                            &::before {
                                content: '예시 숨기기 〈';
                            }

                            &::after {
                                content: '* 더블 클릭하면 이미지가 확대됩니다.';
                                position: absolute;
                                color: darkred;
                                top: 3px;
                                right: -190px;
                                font-size: 11px;
                            }
                            & + .swiper-container {
                                opacity: 1;
                                height: 300px;
                                // overflow: initial;
                            }
                        }
                    }

                    .swiper-container {
                        position: relative;
                        color: #333;
                        background-color: rgb(243, 243, 243);
                        width: 100%;
                        height: 300px;
                        margin-left: auto;
                        margin-right: auto;
                    }
                    .swiper-slide {
                        // background-color: #fff;
                        background-size: cover;
                        background-position: center;
                    }
                    .gallery-top {
                        height: 80%;
                        width: 100%;
                        padding: 13px 0;

                        img {
                            width: 100%;
                        }
                    }
                    .gallery-thumbs {
                        height: 20%;
                        box-sizing: border-box;
                        padding: 10px 0;
                        background-color: rgb(226, 226, 226);

                        img {
                            width: 100%;
                        }
                    }

                    .gallery-thumbs .swiper-slide {
                        width: 33.3333333%;
                        min-height: 64px;
                        opacity: 0.4;
                        @include flexCentered;
                    }
                    .gallery-thumbs .swiper-slide-thumb-active {
                        opacity: 1;
                    }
                }

                .desktop {
                    display: none;

                    @media (min-width: 768px) {
                        display: block;
                    }

                    .album-wrapper {
                        padding: 60px 0 40px;
                        @media (min-width: 768px) {
                            // padding: 0 16px;
                        }

                        img {
                            width: 100%;
                            object-fit: contain;
                        }
                    }
                }
            }
        }
    }

    #index-about-container {
        @include flexCentered();
        position: relative;
        padding: 62px 0;

        @media (min-width: 768px) {
            padding: 82px 0;
        }

        .about-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;

            h1 {
                font-weight: bold;
                font-size: 26px;

                @media (min-width: 768px) {
                    font-size: 34px;
                }
            }

            .line {
                width: 40px;
                height: 8px;
                background-color: #cde0f8;
                margin: 30px 0;
            }

            p {
                font-size: 16px;
                font-weight: 500;

                @media (min-width: 768px) {
                    font-size: 20px;
                }
            }
        }
    }
}
