#mobile-container {
    position: fixed;
    top: 0;
    right: -100%;
    z-index: 9999;
    width: 100%;
    height: 100vh;
    background-color: rgb(238, 238, 238);
    transition: $headerDuration;

    &.active {
        transform: translateX(-100%);
    }

    @media (min-width: 1024px) {
        display: none;
    }

    .head-wrapper {
        height: $headerHeight;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 12px;

        @media (min-width: 768px) {
            padding: 0 24px;
        }
    }

    .right {
        display: flex;
        align-items: center;

        .language {
            margin-right: 12px;
        }

        button {
            background-color: rgba(0, 0, 0, 0);
        }
    }

    .logo {
        img {
            width: 60px;

            @media (min-width: 768px) {
                width: 70px;
            }
        }
    }

    .sign-state-wrapper {
        font-size: 13px;

        ul {
            position: relative;
            width: 100%;
            display: flex;
            border-top: 1px solid #888;
            border-bottom: 1px solid #888;
            padding: 16px 0;

            li {
                flex: 1;
                text-align: center;
                border-right: 1px solid #aaa;

                &:last-child {
                    border: 0;
                }
            }
        }

        .signin-box {
            padding: 32px 0;
            text-align: center;
            visibility: hidden;

            &.active {
                visibility: visible;
            }

            b {
                padding-left: 5px;
                &::before {
                    content: '[';
                }
                &::after {
                    content: ']';
                }
            }
        }
    }

    .menu-wrapper {
        display: flex;
        justify-content: center;
        padding-top: 40px;

        ul {
            width: 80%;
            font-size: 15px;
            font-weight: 500;

            li {
                width: 100%;
                border-bottom: 1px solid #ccc;
                padding: 10px 0;
                margin-bottom: 50px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}
